@import url(https://fonts.googleapis.com/css?family=Roboto:400,100,900);

.App {
  background-color: #fafafa;
  flex-direction: row;
  font-family: 'Roboto';
  width: 100%;
  height: 100%;
}

*:focus{
  outline: none;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
